.element {
  border: 1px solid #eeeeee;
  width: 120px;
  padding: 2px;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 8px;
  transition: 0.1s ease-out;
  font-size: 18px;
}
.element:hover {
  border: 1px solid #fffbfbe3;
  background-color: #ffffffd3;
  color: #222222;
  cursor: pointer;
}

.bar_container{
    background-color: #2222224f;
    height: 100%;
}

.bar{
    display: grid;
    grid-template-columns: auto auto;
    justify-items: center;
    grid-row-gap: 12px;
    margin: auto;
    padding: 5px 5px;
    grid-column-gap: 20px;
}
.bar-container{
    background-color: #2222224f;
    display: flex;
    flex-direction: row;
    height: 100%;
    width: 100%;
}


.hex-value {
  position: relative;
  bottom: 12%;
  right: 8%;
  transform: rotate(-90deg);
  margin: auto;
  color: white;
  float: right;
  border: 1px solid rgba(36, 36, 36, 0);
  background-color: rgba(12, 12, 12, 0.219);
  border-radius: 5px;
  font-size: 14px;
  box-shadow: 5px 5px 10px rgba(2, 2, 2, 0.562);
  transition: 0.1s ease-out;
}

.hex-value:hover {
  bottom: 19%;
  font-size: 20px;
  transform: rotate(0deg);
  cursor: pointer;
}

.main{
    z-index: 1;
}

.sec{
    z-index: 2;
}

.third{
    z-index: 3;
}
